*{
    margin:0;padding:0;
}
body{
    
    background-color:pink
}

.navbar {
    display: inline-flex;
    position: absolute;;
 top:4vh;
    transform:translateX(70%);
   width:40vw;
    background-color: #fff;
   border-radius: 40px;
  height:8vh;
 padding-left:5vh;
    box-shadow: 0 10px 40px rgba(159, 162, 177, .8);
    z-index:1;
}
.nav-item {
    color: #83818c;
    padding: 20px;
    text-decoration: none;
    transition: .3s;
    margin: 0 6px;
    z-index: 1;
    font-family: 'DM Sans', sans-serif;
    font-size:3vh ;
    position: relative;
  
}
.nav-item:before {
    content: "";
    position: absolute;
    bottom: -6px;
    left: 0;
    width: 100%;
    height: 5px;
    background-image: linear-gradient(to right, #43e97b 0%, #38f9d7 100%);
    border-radius: 8px 8px 0 0;
    opacity: 0;
    transition: .3s;
}
.nav-item:not(.is-active):hover:before {
    opacity: 1;
    bottom: 0;
}


.nav-item:not(.is-active):hover {
    color: #333;
}


@media (max-width: 580px) {
    .navbar {
        overflow: auto;
    }
}

.foot{
    position: relative;
    background-color:rgb(37, 36, 36);
    text-align: center;
    height:10vh;
    color:grey;
}
.foot img{top:2vh;
    margin:10px;
}